@font-face {
    font-family: myFont;
    src: url(/fonts/3979.ttf);
}

* {
    padding: 0;
    margin: 0 auto;
    border: 1px solid black;
}

.header {
    /*position: fixed;
    background-color: white;*/
    width: 1260px;
    height: 122px;
    padding-top: 3px;
}

.logo {
    float: left;
    width: 88px;
    height: 106px;
    background-image: url(/img/rabbit1.jpg);
}

.span1 {
    float: left;
    width: 200px;
    padding-top: 12px;
    font-weight: bold;
}

.orange {
    position: relative;
    font-size: 35px;
    color: orange;
    left: 54px;
}

.rabbit {
    font-size: 37px;
    position: relative;
    left: 51px;
    font-family: myFont;
    color: rgb(71, 71, 71)
}

.top-menu {
    float: right;
    padding-top: 11px;
}

.home, .about, .work, .blog, .contact {
    float: left;
    font-size: 23px;
    height: 47px;
    width: 55px;
    text-align: center;
    margin-right: 75px;
}

a {
    display: block;
    text-decoration: none;
    color: grey;
}
/*end_header*/

.border_slider {
    /*margin-top: 122px;
    overflow-x: visible;*/
    background-image: url(/img/slider.jpg);
    background-position-x: center;
}

.slider {
    left: 50%;
    height: 410px;
    width: 1260px;
}

.span2 {
    width: 410px;
    position: relative;
    top: 150px;
    left: -425px;
    font-family: myFont;
    font-size: 38px;
    font-weight: bold;
}

.span3 {
    position: relative;
    color: orange;
    left: 5px;
    padding: 9px 15px;
    padding-left: 30px;
    background-color: rgb(71, 71, 71);
}

.span4 {
    position: relative;
    color: rgb(71, 71, 71);
    background-color: rgb(233, 162, 30); 
    top: 40px;
    padding: 15px 18px;
    padding-left: 30px;
}
/*end_slider*/

.main {
    margin: 0 auto;
    width: 1260px;
}

.content1 {
    padding-top: 28px;
    display: flex;
    flex-wrap: wrap; 
    justify-content: space-evenly; 
    font-size: 16px;
    font-weight: bold; 
}

.item1, .item2, .item3, .item4 {
    width: 24%;
}

.item5, .item6, .item7, .item8 {
    text-align: right;
    color: orange;
    padding-right: 23px;
    width: 23%;
} 
/*end content1*/

.content2 {
    padding-top: 40px; 
}

.span5 {
    text-align: center;
    font-size: 45px;
    text-decoration: underline;
    color: orange;
}

.card {
    display: flex;
    padding-top: 35px;
    justify-content: space-around;
}